<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <el-form-item label="" prop="title">
            <!-- 输入问题 start -->
            <el-input
              class="input-name"
              v-model="searchValidateForm.title"
              placeholder="问题"
            ></el-input>
            <!-- 输入问题 end -->
          </el-form-item>

          <el-form-item label="" prop="applicableModel">
            <!-- 输入机器型号 start -->
            <el-input
              class="input-name"
              v-model="searchValidateForm.applicableModel"
              placeholder="机器型号"
            ></el-input>
            <!-- 输入机器型号 end -->
          </el-form-item>
        </el-form>

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增</el-button
        >
        <!-- 新增 end -->
        <!-- 搜索 end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="loadingTable"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
            <template slot-scope="scope">
              <div>
                <template v-if="value == 'resolvent'">
                  <el-button
                    @click.native.prevent="detailRow(scope.$index, scope.row)"
                    type="text"
                    size="small"
                  >
                    {{ tableContentValue(scope.row, value) }}
                  </el-button>
                </template>
                <template v-else>
                  {{ tableContentValue(scope.row, value) }}
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="editRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                编辑
              </el-button>
              <el-button
                @click.native.prevent="deleteRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog
        class="dialog"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="100px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <!-- 问题标题 start -->
          <el-form-item label="问题标题" prop="title">
            <el-input
              type="text"
              v-model="validateForm.title"
              autocomplete="off"
              placeholder="请输入问题标题"
            ></el-input>
          </el-form-item>
          <!-- 问题标题 end -->

          <!-- 现象 start -->
          <el-form-item label="现象" prop="phenomenon">
            <el-input
              type="text"
              v-model="validateForm.phenomenon"
              autocomplete="off"
              placeholder="请输入现象"
            ></el-input>
          </el-form-item>
          <!-- 现象 end -->

          <!-- 解决方法 start -->
          <el-form-item label="解决方法" prop="resolvent">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span class="fs-18">新增解决方法</span>
                <el-button
                  class="fs-24"
                  style="float: right; padding: 3px 0"
                  type="text"
                  icon="el-icon-circle-plus-outline"
                  @click="onAddResolventItem"
                ></el-button>
              </div>
              <div
                v-for="(item, index) in resolventList"
                :key="index"
                class="resolvent-item"
              >
                <!-- 输入解决方法 start -->
                <el-input
                  ref="resolventInputRef"
                  type="text"
                  v-model="item.title"
                  autocomplete="off"
                  placeholder="请输入解决方法"
                ></el-input>
                <!-- 输入解决方法 end -->

                <!-- 上传文件 start -->
                <el-upload
                  ref="upload"
                  class="upload-demo"
                  action="/system/common/uploadFile"
                  name="file"
                  accept=".jpg,.png, video/*"
                  :on-preview="handlePreview(item.type)"
                  :on-remove="handleRemove(index)"
                  :before-remove="beforeRemove"
                  :before-upload="beforeUpload"
                  :limit="3"
                  :on-exceed="handleExceed"
                  :file-list="item.fileList"
                  :on-success="handleSuccess(index)"
                  v-if="updateUpload"
                >
                  <el-button class="ml-10" type="primary">点击上传</el-button>
                  <div slot="tip" class="el-upload__tip">
                    只能上传一个jpg、png或mp4文件，且不超过100MB
                  </div>
                </el-upload>
                <!-- 上传文件 end -->

                <!-- 删除解决方法 start -->
                <el-button
                  class="fs-24"
                  style="float: right; padding: 3px 0"
                  type="text"
                  icon="el-icon-remove-outline"
                  @click="onDeleteResolventItem(index)"
                ></el-button>
                <!-- 删除解决方法 end -->

                <!-- 预览文件 start -->
                <el-dialog :visible.sync="uploadDialogVisible" append-to-body>
                  <!-- 图片 start -->
                  <template v-if="currentPreType == 1">
                    <img width="100%" :src="previewImgUrl" alt="" />
                  </template>
                  <!-- 图片 end -->

                  <!-- 视频 start -->
                  <template v-if="currentPreType == 2">
                    <video :src="previewVideoUrl" autoplay width="100%"></video>
                  </template>
                  <!-- 视频 end -->

                  <!-- 其他 start -->
                  <template v-if="currentPreType == 3">
                    其他类型的文件暂不支持查看
                  </template>
                  <!-- 其他 end -->
                </el-dialog>
                <!-- 预览文件 end -->
              </div>
            </el-card>
            <!-- <el-input type="text" v-model="validateForm.resolvent" autocomplete="off" placeholder="请输入解决方法"></el-input> -->
          </el-form-item>
          <!-- 解决方法 end -->

          <!-- 视频操作 start -->
          <!-- <el-form-item label="视频操作" prop="video">
            <el-input type="text" v-model="validateForm.video" autocomplete="off" placeholder="请输入视频操作"></el-input>
          </el-form-item> -->
          <!-- 视频操作 end -->

          <!-- 适用机型 start -->
          <el-form-item label="适用机型" prop="applicableModel">
            <el-input
              type="text"
              v-model="validateForm.applicableModel"
              autocomplete="off"
              placeholder="请输入适用机型"
            ></el-input>
          </el-form-item>
          <!-- 适用机型 end -->

          <!-- 创建时间 start -->
          <!-- <el-form-item label="创建时间" prop="createTime">
            <el-input type="text" v-model="validateForm.createTime" autocomplete="off" placeholder="请输入创建时间"></el-input>
          </el-form-item> -->
          <!-- 创建时间 end -->

          <!-- 备注 start -->
          <el-form-item label="备注" prop="notes">
            <el-input
              type="text"
              v-model="validateForm.notes"
              autocomplete="off"
              placeholder="请输入备注"
            ></el-input>
          </el-form-item>
          <!-- 备注 end -->
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->

      <!-- 详情 dialog start -->
      <el-dialog
        class="dialog"
        title=""
        :visible.sync="detailDialogVisible"
        :before-close="detailHandleClose"
        :close-on-click-modal="false"
      >
        <el-collapse v-model="activeName" accordion>
          <el-collapse-item
            v-for="(item, index) in collapseList"
            :key="index"
            :title="item.title"
            :name="index"
          >
            <template v-if="item.type == 1">
              <img width="100%" :src="item.url" alt="" />
            </template>
            <template v-else-if="item.type == 2">
              <video width="100%" :src="item.url" autoplay></video>
            </template>
            <template v-else-if="item.type == 3">
              其他类型的文件暂不支持查看
            </template>
            <template v-else>
              该解决方案没有上传附件
            </template>
          </el-collapse-item>
        </el-collapse>
      </el-dialog>
      <!-- 详情 dialog end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { getFileType } from '../utils/utils.js'
export default {
  name: 'CommonFault',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    return {
      // input: '', // 请输入公司名称
      searchCompanyName: '', // 搜索的公司名称
      tableFields: {
        //导出Excel表格的表头设置
        问题标题: 'title',
        现象: 'phenomenon',
        解决方法: 'resolvent',
        // "视频操作": "video",
        适用机型: 'applicableModel',
        创建时间: 'createTime',
        备注: 'notes'
      },
      tableData: [], // 表格数据
      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      loadingTable: false,

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示dialog对话框
      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      searchValidateForm: {
        title: '',
        applicableModel: ''
      }, // 搜索表单验证对象
      searchVF: {
        title: '',
        applicableModel: ''
      }, // 搜索需要提交的表单
      validateForm: {},
      total: 0,

      dialogTitle: '',

      collapseList: [], // 折叠面板列表

      resolventList: [
        // {
        //   fileList: [{
        //     name: 'food.jpeg',
        //     url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
        //   }], // 文件列表
        //   title: "方法1",
        //   // fileUrl: "http://a.jpg",
        //   // fileName: "文件名称1",
        //   type: 1, // 1、图片 2、视频 3、其他
        // },
        // {
        //   fileList: [{
        //     name: 'food.mp4',
        //     url: require('../../public/LoginBg53.mp4')
        //   }], // 文件列表
        //   title: "方法1",
        //   fileUrl: "http://a.mp4",
        //   fileName: "文件名称1",
        //   type: 2, // 1、图片 2、视频 3、其他
        // },
      ], // 解决方法列表

      previewImgUrl: '', // 预览图片链接
      previewVideoUrl: '', // 预览视频链接
      previewOtherUrl: '', // 预览其他链接
      uploadDialogVisible: false, // 上传dialog组件是否显示
      detailDialogVisible: false, // 详情dialog组件是否显示

      currentPreType: '', // 当前预览类型

      updateUpload: true, // 是否显示上传组件

      activeName: '', // 手风琴当前活动名称

      // 提交表单验证规则
      validateFormRules: {
        // 问题标题
        title: [
          {
            validator: this.checkRequireMethod('问题标题'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 解决方法
        resolvent: [
          {
            validator: this.checkRequireMethod('解决方法'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 适用机型
        applicableModel: [
          {
            validator: this.checkRequireMethod('适用机型'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 搜索表单验证规则
      searchValidateFormRules: {}
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }
    
    this.initTableData()
    this.getValidateForm()
  },
  methods: {
    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        // console.log(this.resolventList);

        // 解决方法是否为空(true不为空，false为空)
        var tag = false

        // 遍历解决方法列表，如果其中某项的title没有值时，则校验不能通过
        this.resolventList.forEach((item) => {
          // console.log(item);
          if (name == '解决方法' && item && !item.title) {
            tag = false // 表示有空值
            return callback(new Error(name + '不能为空'))
          } else {
            tag = true
          }
        })
        if (name == '解决方法' && tag) {
          value = '这里随便给一个值就行' // 随便给一个值使验证通过
        }
        // console.log(value);
        // console.log(name);

        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    },

    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var title = this.searchVF.title
      if (title == '' || !title) {
        title = null
      }

      var applicableModel = this.searchVF.applicableModel
      if (applicableModel == '' || !applicableModel) {
        applicableModel = null
      }

      this.loadingTable = true
      const { data: res } = await this.$http.get('/system/common_fault/list', {
        params: {
          title: title, // 问题标题
          applicableModel: applicableModel, // 机器型号
          pageNum: (this.currentPage - 1) * this.pageSize, // 页码
          pageSize: this.pageSize // 每页条数
        }
      })
      // console.log(res.data);
      if (res.state == '200') {
        this.tableData = res.data.list
        this.total = res.data.total
        // this.$message({
        //   message: res.msg,
        //   type: 'success',
        //   offset: 100,
        //   center: true,
        //   duration: 1000,
        // })
        this.loadingTable = false
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
        return
      }
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      const { data: res } = await this.$http.post(
        '/system/common_fault/add',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post(
        '/system/common_fault/edit',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // console.log(this.searchValidateForm.title, this.searchInput);
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (
        this.searchValidateForm.title == this.searchVF.title &&
        this.searchValidateForm.applicableModel == this.searchVF.applicableModel
      ) {
        return
      }

      // 保存当前搜索的内容
      this.searchVF.title = this.searchValidateForm.title
      this.searchVF.applicableModel = this.searchValidateForm.applicableModel

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log('调用接口')
      this.initTableData()
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.getTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.title = ''
      this.searchValidateForm.applicableModel = ''

      // 重置当前页数
      this.currentPage = 1
      // 重置每页条数
      this.pageSize = 10

      // 重新获取表格数据
      this.getTableData()
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
      this.dialogTitle = '新增常见故障'
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框
      var rowObj = Object.assign({}, row) // 深拷贝row对象
      // rowObj.resolvent = parseFloat(rowObj.resolvent)
      this.validateForm = rowObj
      this.resolventList = JSON.parse(rowObj.resolvent).data
      this.dialogState = 'edit'
      this.dialogTitle = '编辑常见故障'
    },

    // 点击删除时触发
    deleteRow(index, row) {
      // console.log(index, row);
      this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          let data = this.$qs.stringify({
            id: row.id
          })
          const { data: res } = await this.$http.post(
            '/system/common_fault/delete',
            data
          )
          // console.log(res);
          if (res.state == '200') {
            this.$message({
              type: 'success',
              message: res.msg,
              offset: 100,
              center: true,
              duration: 1000
            })
            this.initTableData()
          } else {
            this.$message({
              type: 'error',
              message: res.msg,
              offset: 100,
              center: true,
              duration: 3000
            })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 点击见详情触发
    detailRow(index, row) {
      console.log(index, row)
      this.detailDialogVisible = true
      var data = JSON.parse(row.resolvent).data

      // console.log(data);
      var collapseList = []
      data.forEach((item) => {
        collapseList.push({
          title: item && item.title,
          url:
            item && item.fileList && item.fileList[0] && item.fileList[0].url,
          type: item && item.type // 1、图片 2、视频 3、其他
        })
      })
      // console.log(collapseList);
      this.collapseList = collapseList
    },

    // 编辑dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
      this.resolventList = [] // 将解决方法列表置为空
    },

    // 编辑dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
      this.resolventList = [] // 将解决方法列表置为空
    },

    // 详情dialog 右上角关闭按钮
    detailHandleClose(done) {
      this.activeName = '' // 关闭弹窗使手风琴也关闭
      done() // 关闭dialog对话框
      this.detailDialogVisible = false
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // var urlList = this.resolventList.map((item) => {
          //   return item.fileList[0].url
          // })

          // var video = urlList.join(",")
          // this.validateForm.video = video

          var data = {
            data: this.resolventList
          }
          this.validateForm.resolvent = JSON.stringify(data)
          console.log(this.validateForm)
          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
          this.getValidateForm() // 将validateForm对象中各个属性清空
          this.resolventList = [] // 将解决方法列表置为空
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    // 点击新增解决方法项
    onAddResolventItem() {
      console.log('新增解决步骤')
      // 在 resolventList 中新增对象即可新增一行数据，后续可以在对象中添加新的属性
      this.resolventList.push({
        title: ''
      })
    },

    // 点击删除解决方法项
    onDeleteResolventItem(index) {
      console.log('删除解决步骤')
      // console.log(index);
      this.resolventList.splice(index, 1)

      this.$refs.resolventInputRef[0].focus()
      this.$refs.resolventInputRef[0].blur()
    },

    // 删除文件
    handleRemove(index) {
      return (file, fileList) => {
        // 注意：不能删除原对象，改变原对象后会使v-model属性失效（找不到原对象了所以失效），所以只能来修改原对象达到效果
        var currrentItem = this.resolventList[index]
        if (currrentItem) {
          if (currrentItem.fileList) {
            currrentItem.fileList = []
          }
          if (currrentItem.type) {
            currrentItem.type = ''
          }
        }
      }
    },
    // 预览文件
    handlePreview(type) {
      return (file) => {
        console.log(type)
        this.currentPreType = type
        this.uploadDialogVisible = true
        if (type == 1) {
          this.previewImgUrl = file.url
        } else if (type == 2) {
          this.previewVideoUrl = file.url
        } else if (type == 3) {
          this.previewOtherUrl = file.url
        }
      }
    },
    // 限制文件数量
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 3 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      )
    },
    // 删除文件之前的操作
    beforeRemove(file, fileList) {
      // return this.$confirm(`确定移除 ${file.name}？`)
    },

    // 上传文件之前的操作
    beforeUpload(file) {
      var fileType = file.type
      console.log(fileType)

      if (
        fileType !== 'image/jpeg' &&
        fileType !== 'image/png' &&
        fileType !== 'video/mp4'
      ) {
        this.$message.warning(`当前仅能上传jpg、png或mp4文件`)
        return false
      }
    },

    /**
     * 更新upload组件的方法
     */
    reloadUpload() {
      // this.$forceUpdate()
      // 移除组件
      this.updateUpload = false
      // 在组件移除后，重新渲染组件
      // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
      this.$nextTick(() => {
        this.updateUpload = true
      })
    },
    // 文件上传成功时的操作
    handleSuccess(index) {
      return (response, file, fileList) => {
        console.log(file)
        // 限制文件大小500MB
        if (file.size > 500 * 1024 * 1024) {
          console.log('上传文件大小不能大于500MB')
          this.$notify({
            title: '文件过大',
            message: '上传文件大小不能大于500MB'
          })
          return false
        }

        // 在file对象中添加url属性
        file.url = '/' + file.response.data
        // console.log(file);

        this.resolventList[index].fileList = [file]

        this.reloadUpload()

        var fileType = getFileType(file.name)
        if (fileType == 'image') {
          // 图片类型
          this.resolventList[index].type = 1
        } else if (fileType == 'video') {
          // 视频类型
          this.resolventList[index].type = 2
        } else if (fileType == 'other') {
          //  其他类型
          this.resolventList[index].type = 3
        }
      }
    }
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value);
        var result = ''
        if (value == 'resolvent') {
          // console.log(row[value]);
          var data = {}
          try {
            data = JSON.parse(row[value])
            result = '见详情'
          } catch (error) {
            result = row[value]
          }
        } else {
          result = row[value]
        }
        return result
      }
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  }
}
</script>

