/**
 * 根据文件名判断文件类型
 * @param {*} fileName 文件名
 * @returns
 */
export function getFileType(fileName) {
  // 后缀获取
  var suffix = "";
  var result = "";
  try {
    var flieArr = fileName.split(".");
    suffix = flieArr[flieArr.length - 1];
  } catch (err) {
    suffix = "";
  }
  // fileName无后缀返回 false
  if (!suffix) {
    result = false;
    return result;
  }
  // 图片格式
  var imglist = ["png", "jpg", "jpeg", "bmp", "gif", "webp", "heif", "heic"];
  // 进行图片匹配
  result = imglist.some(function(item) {
    return item == suffix;
  });
  if (result) {
    result = "image";
    return result;
  }

  // 匹配 视频
  var videolist = ["mp4", "3gp", "webm", "mkv"];
  result = videolist.some(function(item) {
    return item == suffix;
  });
  if (result) {
    result = "video";
    return result;
  }

  // 其他 文件类型
  result = "other";
  return result;
}
